"use client";

import { useState, useEffect, useCallback, use } from "react";
import { useUser } from "@clerk/nextjs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { CheckCircle, Copy, ExternalLink, Bot } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import Link from "next/link";
import CodeSnippet from "@/components/CodeSnippet";

interface DiscordConfig {
  configId: string;
  agentId: string;
  clientId: string;
  guildId?: string;
  isActive: boolean;
}

export default function DiscordIntegrationPage({
  searchParams,
}: {
  searchParams: Promise<{ agentId?: string }>;
}) {
  const { user } = useUser();
  const { toast } = useToast();
  const resolvedSearchParams = use(searchParams);
  const agentId = (resolvedSearchParams?.agentId || '').trim();
  
  const [config, setConfig] = useState<DiscordConfig | null>(null);
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    botToken: '',
    clientId: '',
    guildId: ''
  });

  const fetchConfig = useCallback(async () => {
    try {
      const response = await fetch(`/api/discord/bot-config?agentId=${agentId}`);
      const data = await response.json();
      if (data.success && data.config) {
        setConfig(data.config);
      }
    } catch (error) {
      console.error('Failed to fetch config:', error);
    }
  }, [agentId]);
  
  useEffect(() => {
    if (user && agentId) {
      fetchConfig();
    }
  }, [user, agentId, fetchConfig]);

  const handleSave = async () => {
    if (!formData.botToken || !formData.clientId) {
      toast({
        title: "Error",
        description: "Bot Token and Client ID are required",
        variant: "destructive"
      });
      return;
    }

    setLoading(true);
    try {
      const response = await fetch('/api/discord/bot-config', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          agentId,
          botToken: formData.botToken,
          clientId: formData.clientId,
          guildId: formData.guildId || null
        })
      });

      const data = await response.json();
      if (data.success) {
        toast({
          title: "Success",
          description: "Discord bot configured successfully!"
        });
        await fetchConfig();
        setFormData({ botToken: '', clientId: '', guildId: '' });
      } else {
        throw new Error(data.error || 'Failed to save configuration');
      }
    } catch (error) {
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : 'Failed to save configuration',
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const handleHostBot = async () => {
    if (!formData.botToken || !formData.clientId) {
      toast({
        title: "Error",
        description: "Bot Token and Client ID are required",
        variant: "destructive"
      });
      return;
    }

    setLoading(true);
    try {
      // First save the configuration
      const response = await fetch('/api/discord/bot-config', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          agentId,
          botToken: formData.botToken,
          clientId: formData.clientId,
          guildId: formData.guildId || null
        })
      });

      const data = await response.json();
      if (data.success) {
        // Start hosting the bot
        const hostResponse = await fetch('/api/discord/host-bot', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            agentId,
            configId: data.configId
          })
        });

        const hostData = await hostResponse.json();
        if (hostData.success) {
          toast({
            title: "Success!",
            description: "Discord bot is now hosted and running on our servers!"
          });
          await fetchConfig();
          setFormData({ botToken: '', clientId: '', guildId: '' });
        } else {
          throw new Error(hostData.error || 'Failed to start hosting bot');
        }
      } else {
        throw new Error(data.error || 'Failed to save configuration');
      }
    } catch (error) {
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : 'Failed to host bot',
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const handleRemove = async () => {
    setLoading(true);
    try {
      const response = await fetch(`/api/discord/bot-config?agentId=${agentId}`, {
        method: 'DELETE'
      });

      const data = await response.json();
      if (data.success) {
        toast({
          title: "Success",
          description: "Discord bot configuration removed"
        });
        setConfig(null);
      } else {
        throw new Error(data.error || 'Failed to remove configuration');
      }
    } catch (error) {
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : 'Failed to remove configuration',
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
    toast({
      title: "Copied!",
      description: "Text copied to clipboard"
    });
  };

  const inviteUrl = config ? 
    `https://discord.com/api/oauth2/authorize?client_id=${config.clientId}&permissions=2048&scope=bot` : 
    '';

  if (!user) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardContent className="p-6 text-center">
            <p>Please sign in to configure Discord integration.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (!agentId) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardContent className="p-6 text-center">
            <p>No agent ID provided. Please select an agent first.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Discord Integration</h1>
          <p className="text-gray-600">Connect your agent to Discord with our hosted bot service</p>
        </div>

        {config ? (
          <Card className="mb-6">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <CheckCircle className="h-5 w-5 text-green-500" />
                Discord Bot Active
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <Alert>
                  <Bot className="h-4 w-4" />
                  <AlertDescription>
                    Your Discord bot is configured and running on our servers. Users can now chat with your agent via direct messages or by mentioning the bot in servers.
                  </AlertDescription>
                </Alert>
                
                <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div>
                    <p className="font-medium">Bot Invite URL</p>
                    <p className="text-sm text-gray-500">Add your bot to Discord servers/groups</p>
                  </div>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => copyToClipboard(inviteUrl)}
                  >
                    <Copy className="h-4 w-4 mr-2" />
                    Copy URL
                  </Button>
                </div>

                <Alert>
                  <Bot className="h-4 w-4" />
                  <AlertDescription>
                    Click the invite link to add your bot to any Discord server or group. The bot will respond to direct messages and mentions with your AI agent.
                  </AlertDescription>
                </Alert>

                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    onClick={() => window.open(inviteUrl, '_blank')}
                    className="flex-1"
                  >
                    <ExternalLink className="h-4 w-4 mr-2" />
                    Add to Discord Server
                  </Button>
                  <Button
                    variant="destructive"
                    onClick={handleRemove}
                    disabled={loading}
                    className="flex-1"
                  >
                    Stop Hosting
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        ) : (
          <Card className="mb-6">
            <CardHeader>
              <CardTitle>Configure Discord Bot</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <Alert>
                  <Bot className="h-4 w-4" />
                  <AlertDescription>
                    We'll host the Discord bot for you! Just provide your bot credentials and we'll handle the rest.
                  </AlertDescription>
                </Alert>

                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium mb-2">Bot Token</label>
                    <Input
                      type="password"
                      placeholder="Your Discord bot token"
                      value={formData.botToken}
                      onChange={(e) => setFormData(prev => ({ ...prev, botToken: e.target.value }))}
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2">Client ID</label>
                    <Input
                      placeholder="Your Discord application client ID"
                      value={formData.clientId}
                      onChange={(e) => setFormData(prev => ({ ...prev, clientId: e.target.value }))}
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2">Guild ID (Optional)</label>
                    <Input
                      placeholder="Specific server ID (leave empty for all servers)"
                      value={formData.guildId}
                      onChange={(e) => setFormData(prev => ({ ...prev, guildId: e.target.value }))}
                    />
                  </div>

                  <div className="flex gap-2">
                    <Button
                      onClick={handleSave}
                      disabled={loading}
                      className="flex-1"
                    >
                      {loading ? 'Configuring...' : 'Configure Bot'}
                    </Button>
                    <Button
                      onClick={handleHostBot}
                      disabled={loading || !formData.botToken || !formData.clientId}
                      className="flex-1"
                      variant="default"
                    >
                      {loading ? 'Hosting...' : 'Host Bot'}
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        <Card>
          <CardHeader>
            <CardTitle>Setup Instructions</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <h3 className="font-medium mb-2">1. Create Discord Application</h3>
                <p className="text-sm text-gray-600 mb-2">
                  Go to the <Link href="https://discord.com/developers/applications" className="text-blue-600 hover:underline">Discord Developer Portal</Link> and create a new application.
                </p>
              </div>

              <div>
                <h3 className="font-medium mb-2">2. Create Bot User</h3>
                <p className="text-sm text-gray-600 mb-2">
                  In your application, go to the "Bot" section and create a bot user. Copy the bot token.
                </p>
              </div>

              <div>
                <h3 className="font-medium mb-2">3. Configure Bot Permissions</h3>
                <p className="text-sm text-gray-600 mb-2">
                  Enable the following bot permissions:
                </p>
                <ul className="text-sm text-gray-600 list-disc list-inside ml-4">
                  <li>Send Messages</li>
                  <li>Read Message History</li>
                  <li>Use Slash Commands</li>
                </ul>
              </div>

              <div>
                <h3 className="font-medium mb-2">4. Get Your Credentials</h3>
                <p className="text-sm text-gray-600 mb-2">
                  Copy your Bot Token and Client ID from the Discord Developer Portal and paste them above.
                </p>
              </div>

              <div>
                <h3 className="font-medium mb-2">5. We Handle the Rest!</h3>
                <p className="text-sm text-gray-600">
                  Once configured, our servers will run your Discord bot 24/7. Users can chat with your agent via DMs or by mentioning the bot in servers.
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
